<template>
    <el-container v-if="type=== 'iFun'">
        <el-aside width="65%">
            <el-input placeholder="" v-model="form.zhTitle"/>

            <el-input placeholder="" v-model="form.enTitle"/>

            <el-input
                    type="textarea"
                    autosize
                    placeholder=""
                    v-model="form.abstract">
            </el-input>

            <el-select value=""
                       v-model="form.label"
                       multiple
                       filterable
                       allow-create
                       default-first-option
                       placeholder="">
                <el-option
                        v-for="item in labelList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>

            <el-input placeholder="" v-model="form.zhVvt"/>

            <el-input placeholder="" v-model="form.enVvt"/>
        </el-aside>
        <el-main>
            <el-select placeholder="" v-model="form.class" value="" size="mini">
                <el-option label="" value="funny"/>
                <el-option label="" value="movie"/>
                <el-option label="" value="entertainment"/>
                <el-option label="" value="culture"/>
                <el-option label="" value="travel"/>
                <el-option label="" value="arts"/>
                <el-option label="" value="foods"/>
                <el-option label="" value="sports"/>
                <el-option label="" value="technology"/>
            </el-select>


            <el-select placeholder="" v-model="form.difficulty" value="" size="mini">
                <el-option label="" value="simple"/>
                <el-option label="" value="easily"/>
                <el-option label="" value="difficulty"/>
            </el-select>
        </el-main>
    </el-container>
</template>

<script>
    export default {
        props:{
            type:String,
            form:{
                type: Object,
                default (){
                    return {}
                }
            },
        },
        data() {
            return {
                labelList: [],
            }
        },
        mounted(){
        },
    }
</script>

<style scoped>
    .el-main .el-select{
        display: block;
    }
    .el-main {
        padding: 0 20px;
    }

    .el-aside *, .el-main * {
        margin: 15px 0;
    }
</style>